.btn {
    border: 1px solid black;
    background-color: white;
    border-radius: 2rem;
    padding: 0.5rem 1rem;
    margin: 2rem 0 2rem 2rem;
    color: black;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
}

.btn:hover {
    color: white;
    background-color: black;
}

.btn-dark {
    color: white;
    background-color: black;
    border: 1px solid white;
    border-radius: 2rem;
    margin: 2rem 0 2rem 2rem;
    padding: 0.5rem 1rem;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
}

.btn-dark:hover {
    /* color: black; */
    background-color: #353b48;
}


